<template>
	<section>
		<div  class="box">
			<div v-title></div>
			<div v-if="question==index" v-for="(item,index) in info">
				<!-- <div v-title>{{item.question}}</div> -->
				<h1 style="font-size: 16px;font-weight: bold;">{{item.question}}</h1>
				<p v-for="item1 in item.answer">{{item1}}</p>
			</div>
		</div>
	</section>
</template>
<script type="text/javascript">
	export default{
		data(){
			return{
				info:[
					{
						question:'',
						answer:['','']
					},
					{
						question:'',
						answer:['']
					},				
					{
						question:'',
						answer:['']
					},				
					{
						question:'',
						answer:['','']
					},				
					{
						question:'',
						answer:['400-008-1027']
					},				{
						question:'',
						answer:['','']
					},
				],
				question:'haha'
			}
		},
		mounted(){
			this.question = this.$route.query.question;
		}
	}
</script>
<style type="text/css" scoped>
	section{
		background: #f6f6f6;
		height:100vh;
	}
	.box{
		font-size: 14px;
		text-align: left;
		padding:15px;
		line-height: 25px;
		padding-top:20px;
		background: #fff;
		box-sizing: border-box;
	}
</style>



